@font-face {
  font-family: "Adventure";
  src: url("/fonts/Adventure.otf") format("opentype");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
body {
  background-color: #b00404;
  min-height: 100vh;
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}
.glowing-container {
  position: relative;
  background-color: #c80505;
  border-radius: 15px;
  box-shadow: 0 0 50px 20px rgba(255, 0, 0, 0.4);
  z-index: 1;
  overflow: hidden;
}
.glowing-container::before {
  content: "";
  position: absolute;
  top: -20%;
  left: -20%;
  right: -20%;
  bottom: -20%;
  background: radial-gradient(circle, rgba(194, 1, 1, 0.3) 0%, transparent 60%);
  filter: blur(80px);
  z-index: -1;
  animation: pulse-glow 3s infinite alternate;
}
@keyframes pulse-glow {
  from {
    transform: scale(0.95);
    opacity: 0.7;
  }
  to {
    transform: scale(1.05);
    opacity: 1;
  }
}
.text-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.animated-letter {
  display: inline-block;
  opacity: 0;
  transform: translateY(20px) scale(0.8);
  animation: popIn 0.6s ease-out forwards;
}
@keyframes popIn {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.7);
  }
  50% {
    opacity: 1;
    transform: translateY(-5px) scale(1.05);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
.dropping-texts {
  display: inline-block;
  width: 100%;
  display: flex;
}
.dropping-texts > div {
  opacity: 0;
  position: absolute;
}
.dropping-texts > div:nth-child(1) {
  animation: roll 5s linear infinite 0s;
}
.dropping-texts > div:nth-child(2) {
  animation: roll 5s linear infinite 1s;
}
.dropping-texts > div:nth-child(3) {
  animation: roll 5s linear infinite 2s;
}
.dropping-texts > div:nth-child(4) {
  animation: roll2 5s linear infinite 3s;
}
@keyframes roll {
  0% {
    font-size: 0px;
    opacity: 0;
    margin-top: 0px;
    transform: rotate(-25deg);
  }
  3% {
    opacity: 1;
    transform: rotate(0deg);
  }
  5% {
    font-size: inherit;
    opacity: 1;
    margin-left: 0px;
    margin-top: 0px;
  }
  20% {
    font-size: inherit;
    opacity: 1;
    margin-left: 0px;
    margin-top: 0px;
    transform: rotate(0deg);
  }
  27% {
    font-size: 0px;
    opacity: 0.5;
    margin-left: 20px;
    margin-top: 100px;
  }
  100% {
    font-size: 0px;
    opacity: 0;
    margin-left: -30px;
    margin-top: 0px;
    transform: rotate(15deg);
  }
}
@keyframes roll2 {
  0% {
    font-size: 0px;
    opacity: 0;
    margin-left: -30px;
    margin-top: 0px;
    transform: rotate(-25deg);
  }
  3% {
    opacity: 1;
    transform: rotate(0deg);
  }
  5% {
    font-size: inherit;
    opacity: 1;
    margin-left: 0px;
    margin-top: 0px;
  }
  30% {
    font-size: inherit;
    opacity: 1;
    margin-left: 0px;
    margin-top: 0px;
    transform: rotate(0deg);
  }
  37% {
    font-size: 1500px;
    opacity: 0;
    margin-left: -1000px;
    margin-top: -800px;
  }
  100% {
    font-size: 0px;
    opacity: 0;
    margin-left: -30px;
    margin-top: 0px;
    transform: rotate(15deg);
  }
}
